import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import 'semantic-ui-css/semantic.min.css';
import {
    Grid,
    Button,
    Label,
    Container,
    Header,
    Loader,
    Dimmer,
    Segment,
    Card
} from "semantic-ui-react";
import Nav from '../Nav/Nav';

export default function Content() {

    const navigate = useNavigate();
    const [loading, setLoading] = useState(true);
    const [addr, setAddr] = useState(null);


    useEffect(() => {
        async function fetchData() {
            try {
                const res = await axios.get('/account', {
                    responseType: 'json',
                    withCredentials: true,
                });
                setAddr(res.data);
            } catch (error) {
                navigate('/login', { replace: true });
            } finally {
                setLoading(false);
            }
        }

        void fetchData();
    }, [navigate, setAddr, setLoading]);


    const handleClick = ({ Addr, Pool }) => {
        navigate(`/address/${Addr}`, {
            state: {
                pool: Pool,
                addr: Addr
            },
        });
    };

    if (loading) {
        return (
            <div>
                <Container className='content'>
                    <Grid textAlign='center' style={{ height: '50vh' }} verticalAlign='middle'>
                        <Grid.Row style={{ maxWidth: 450 }}>
                            <Grid.Column>
                                <Dimmer active inverted>
                                    <Loader size="large" />
                                </Dimmer>
                            </Grid.Column>
                        </Grid.Row>
                    </Grid>
                </Container>
            </div>
        )
    }

    return (
        <div>
            <Nav />
            <Container className='content'>
                <Grid>
                    <Grid.Row>
                        <Grid.Column>
                            {addr.Data ?
                                <FullTable addr={addr} handleClick={handleClick} navigate={navigate} /> :
                                <LinkToCreate navigate={navigate} />}
                        </Grid.Column>
                    </Grid.Row>
                </Grid>
            </Container>
        </div>
    );
}

const FullTable = ({ addr, navigate }) => {
    return (
        <div>

            <Grid>
                <Grid.Row>
                    <Grid.Column>
                        <Button
                            basic
                            compact
                            content="Add another address?"
                            color='black'
                            type='button'
                            floated="left"
                            size='large'
                            icon='plus'
                            onClick={() => (navigate('/create', { replace: true }))}>
                        </Button>
                    </Grid.Column>
                </Grid.Row>
            </Grid>
            <Card.Group itemsPerRow='1'>
                <ContentTable addr={addr.Data} />
            </Card.Group>
        </div>
    )
};

const ContentTable = ({ addr }) => {

    const navigate = useNavigate();

    const handleSettingsClick = ({ Addr, Pool }) => {
        navigate(`/settings/${Addr}`, {
            state: {
                pool: Pool,
                addr: Addr
            }
        });
    };

    const handleStateClick = ({ Addr, Pool }) => {
        navigate(`/state/${Addr}`, {
            state: {
                pool: Pool,
                addr: Addr,
            }
        });
    };


    if (addr) {
        return (
            addr.map(item => (
                <Card fluid color={item.MonitorAddr ? 'teal' : 'red'} key={item.Id.toString()}>
                    <Card.Content>
                        <Label circular color='blue'>{item.Pool.toString()}</Label> <Label circular>{item.Addr.slice(0, 10) + '...' + item.Addr.slice(-10)}</Label>
                    </Card.Content>
                    <Card.Content>
                        <Card.Description>
                            Monitoring Address: &nbsp;&nbsp;&nbsp;<Label circular color={item.MonitorAddr ? "green" : "red"}>{item.MonitorAddr ? "Yes" : "No"}</Label>
                        </Card.Description>
                    </Card.Content>
                    <Card.Content>
                        <Card.Description>
                            Monitoring Block:  &nbsp;&nbsp;&nbsp;<Label circular color={item.MonitorBlock ? "green" : "red"}>{item.MonitorBlock ? "Yes" : "No"}</Label>
                        </Card.Description>
                    </Card.Content>
                    <Card.Content extra>
                        <div className='ui two buttons'>
                            <Button
                                basic
                                fluid
                                size='large'
                                content='Settings'
                                type='button'
                                color="black"
                                className='ui button'
                                icon='cogs'
                                onClick={() => handleSettingsClick(item)} />
                            <Button
                                basic
                                fluid
                                size='large'
                                content="State"
                                type='button'
                                color='black'
                                className='ui button'
                                icon='eye'
                                onClick={() => handleStateClick(item)} />
                        </div>
                    </Card.Content>
                </Card >
            ))
        );
    }
};

const LinkToCreate = ({ navigate }) => {
    return (
        <div>
            <Grid textAlign='center' style={{ height: '50vh' }} verticalAlign='middle'>
                <Grid.Column style={{ maxWidth: 450 }}>
                    <Container className='content'>
                        <Segment size='large' padded>
                            <Header sub>Welcome to the 2miners monitoring bot!</Header>
                            <br />
                            <Button
                                basic
                                compact
                                content='Add address for monitoring?'
                                color='black'
                                type='button'
                                size='large'
                                icon='plus'
                                onClick={() => (navigate('/create', { replace: true }))} />
                        </Segment>
                    </Container>
                </Grid.Column>
            </Grid>
        </div>
    );
};






