import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import axios from 'axios';
import {
    Segment,
    Form,
    Button,
    Grid,
    Header,
    Message,
    Container,
    Dimmer,
    Loader
} from "semantic-ui-react";
import './auth.css';

const SuccessMessage = () => {

    const navigate = useNavigate();

    setTimeout(() => {
        navigate('/', { replace: true });
    }, 5000)

    return (
        <Grid textAlign='center' style={{ height: '100vh' }} verticalAlign='middle'>
            <Grid.Column style={{ maxWidth: 450 }}>
                <Message
                    success
                    header='Check your email.'
                    content='Click on the verification link to activate your account.
                    The link is active for one hour.'
                />
            </Grid.Column>
        </Grid>
    );
};

const StartForm = ({ login, setLogin, password, setPassword, handleRegister, error }) => {

    const navigate = useNavigate();

    const handleClick = () => {
        navigate('/login', { replace: true });
    };

    return (
        <Grid textAlign='center' style={{ height: '100vh' }} verticalAlign='middle'>
            <Grid.Column style={{ maxWidth: 450 }}>
                <Header as='h3' color='red' textAlign='center'>
                    {error && <div className="error">{error}</div>}
                </Header>
                <Form size='large'>
                    <Segment>
                        <Header as='h2' floated='left'>Registration</Header>
                        <Form.Input
                            type='email'
                            fluid icon='user'
                            iconPosition='left'
                            placeholder='E-mail address'
                            value={login}
                            onChange={(e) => setLogin(e.target.value)}
                        />
                        <Form.Input
                            fluid
                            icon='lock'
                            iconPosition='left'
                            placeholder='Password'
                            type='password'
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                        />
                        <Button
                            basic
                            fluid
                            size='large'
                            type='button'
                            color='black'
                            content="Sign up"
                            className="ui button"
                            onClick={handleRegister}
                        />
                    </Segment>
                </Form>
                <Message>
                    Have an account? <span className="link" onClick={handleClick}>Sign In</span>
                </Message>
            </Grid.Column>
        </Grid>
    );
};


export default function RegisterForm() {

    const [login, setLogin] = useState('');
    const [password, setPassword] = useState('');
    const [error, setError] = useState('');
    const [success, setSuccess] = useState(false);
    const [loading, setLoading] = useState(false);
    const navigate = useNavigate();

    useEffect(() => {
        async function fetchData() {
            try {
                await axios.get('/account', {
                    responseType: 'json',
                    withCredentials: true,
                });
                navigate('/', { replace: true })
            } catch (error) {
                navigate('/register', { replace: true });
            } finally {
                setLoading(false);
            }
        }

        void fetchData();
    }, [navigate]);

    const handleRegister = async () => {
        setLoading(true);
        axios.post('/account/register', { login, password },
            { withCredentials: true }
        ).then(() => {
            setSuccess(true);
        }).catch(() => {
            setError('Registration error. Please try again.');
        }).finally(() => {
            setLoading(false);
        });
    };


    if (loading) {
        return (
            <div>
                <Container className='content'>
                    <Grid textAlign='center' style={{ height: '50vh' }} verticalAlign='middle'>
                        <Grid.Row style={{ maxWidth: 450 }}>
                            <Grid.Column>
                                <Dimmer active inverted>
                                    <Loader size="large" />
                                </Dimmer>
                            </Grid.Column>
                        </Grid.Row>
                    </Grid>
                </Container>
            </div>
        )
    }

    return (
        <div>
            {success ? <SuccessMessage /> : <StartForm login={login} setLogin={setLogin} password={password} setPassword={setPassword} handleRegister={handleRegister} error={error} />}
        </div>
    );
}