import React, { useState, useEffect } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import axios from 'axios';
import 'semantic-ui-css/semantic.min.css';
import {
    Container,
    Grid,
    GridRow,
    Breadcrumb,
    Loader,
    Dimmer,
    Button,
    Form,
    Checkbox
} from "semantic-ui-react";
import { PoolMap } from '../Map/PoolMap';
import Nav from '../Nav/Nav';

export default function Settings() {

    const location = useLocation();
    const navigate = useNavigate();
    const state = location.state;

    const [notify, setNotify] = useState(null);
    const [monitor, setMonitor] = useState();
    const [block, setBlock] = useState();

    const [json, setJson] = useState(null);
    const [loading, setLoading] = useState(true);

    const pool = state.pool;
    const addr = state.addr;
    const breadcrumbName = PoolMap.get(pool);

    useEffect(() => {
        async function fetchData() {
            try {
                const response = await axios.post('/address/state', {
                    pool: pool,
                    addr: addr
                }, { withCredentials: true });
                setJson(response.data);
            } catch (error) {
                console.log(error);
                // navigate('/');
            } finally {
                setLoading(false);
            }

        }
        if (pool && addr) {
            void fetchData();
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [pool, addr, navigate]);

    useEffect(() => {
        if (json) {
            setNotify(json.Notify);
            setMonitor(json.MonitorAddr);
            setBlock(json.MonitorBlock);
        }
    }, [json]);

    const handleBack = async () => {
        navigate('/');
    };

    const handleUpdateRange = async (e) => {
        setNotify(e.target.value);
        function fetchData() {
            try {
                axios.put('/address', {
                    pool: pool,
                    addr: addr,
                    notify: Number(e.target.value),
                    monitoraddr: monitor,
                    monitorblock: block,
                }, { withCredentials: true, });
            } catch (error) {
                console.log(error);
            }
        }
        void fetchData();
    };

    const handleCheckedBlock = async () => {
        setBlock(!block);
        function fetchData() {
            try {
                axios.put('/address', {
                    pool: pool,
                    addr: addr,
                    notify: notify,
                    monitoraddr: monitor,
                    monitorblock: !block,
                }, { withCredentials: true, });
            } catch (error) {
                console.log(error);
            }
        }
        fetchData();
    };

    const handleCheckedMonitor = async () => {
        setMonitor(!monitor);
        function fetchData() {
            try {
                axios.put('/address', {
                    pool: pool,
                    addr: addr,
                    notify: notify,
                    monitoraddr: !monitor,
                    monitorblock: block,
                }, { withCredentials: true, });
            } catch (error) {
                console.log(error);
            }
        }
        fetchData();
    };

    const handleDelete = () => {
        axios.delete('/address', {
            data: { pool: pool, addr: addr }
        }, { withCredentials: true }
        ).then(() => {
            navigate('/');
        });
    };

    const Top = () => {
        return (
            <GridRow>
                <Grid.Column>
                    <Breadcrumb>
                        <Breadcrumb.Section as='a' href='/'>Home</Breadcrumb.Section>
                        <Breadcrumb.Divider />
                        <Breadcrumb.Section>{breadcrumbName.toString()}</Breadcrumb.Section>
                        <Breadcrumb.Divider />
                        <Breadcrumb.Section active>{addr.slice(0, 8) + '...' + addr.slice(-8, 8)}</Breadcrumb.Section>
                    </Breadcrumb>
                </Grid.Column>
            </GridRow>
        )
    }

    const Content = () => {
        return (
            <div>
                <Nav />
                <Container className='content'>
                    <Form>
                        <Grid>
                            <Top />
                            <GridRow>
                                <Grid.Column>
                                    <Form.Field>
                                        <div>Send email if a worker goes offline over {notify} minutes</div>
                                        <Form.Input
                                            type='range'
                                            min={1}
                                            max={60}
                                            value={notify}
                                            onChange={handleUpdateRange}
                                        />
                                    </Form.Field>
                                </Grid.Column>
                            </GridRow>
                            <GridRow>
                                <Grid.Column width="8">
                                    <Form.Field>
                                        <Checkbox
                                            label='Monitoring'
                                            checked={monitor}
                                            onChange={handleCheckedMonitor} />
                                    </Form.Field>
                                </Grid.Column>
                                <Grid.Column width="8">
                                    <Form.Field>
                                        <Checkbox
                                            label='New blocks'
                                            checked={block}
                                            onChange={handleCheckedBlock} />
                                    </Form.Field>
                                </Grid.Column>
                            </GridRow>
                            <GridRow>
                                <Grid.Column width="8">
                                    <Button
                                        size='large'
                                        basic
                                        fluid
                                        content='Back'
                                        type='button'
                                        color="black"
                                        className="ui button"
                                        icon='arrow alternate circle left outline'
                                        onClick={handleBack}
                                    />
                                </Grid.Column>
                                <Grid.Column width="8">
                                    <Button
                                        size='large'
                                        basic
                                        fluid
                                        content="Delete"
                                        type='button'
                                        color="red"
                                        className="ui button"
                                        icon='delete'
                                        onClick={handleDelete} />
                                </Grid.Column>
                            </GridRow>
                        </Grid>
                    </Form>
                </Container>
            </div >
        )
    };

    if (loading) {
        return (
            <div>
                <Container className='content'>
                    <Grid textAlign='center' style={{ height: '50vh' }} verticalAlign='middle'>
                        <Grid.Row style={{ maxWidth: 450 }}>
                            <Grid.Column>
                                <Dimmer active inverted>
                                    <Loader size="large" />
                                </Dimmer>
                            </Grid.Column>
                        </Grid.Row>
                    </Grid>
                </Container>
            </div>
        )
    }

    return (
        <div>
            {json.toString() && <Content />}
        </div >
    );
}