import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import {
    Container,
    Segment,
    Header,
    Divider,
    Grid,
    GridRow,
    GridColumn,
    Button,
    Dimmer,
    Loader
} from "semantic-ui-react";
import Nav from '../Nav/Nav';
import "./profile.css";

export default function Profile() {

    const navigate = useNavigate();
    const [loading, setLoading] = useState(true);
    const [json, setJson] = useState([]);


    useEffect(() => {
        async function fetchData() {
            try {
                const res = await axios.get('/account', {
                    responseType: 'json',
                    withCredentials: true,
                });
                setJson(res.data);
                setLoading(false);
            } catch (error) {
                navigate('/login', { replace: true });
            }
        }
        void fetchData();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [navigate, setJson, setLoading]);

    const handleBack = () => {
        navigate('/');
    };

    const handleDelete = () => {
        try {
            axios.delete('/account', {
                data: { user: "" },
            }, { withCredentials: true })
        } catch (error) {
            navigate('/', { replace: true });
        } finally {
            navigate('/', { replace: true });
        }
    };

    if (loading) {
        return (
            <div>
                <Nav />
                <Container className='content'>
                    <Grid textAlign='center' style={{ height: '50vh' }} verticalAlign='middle'>
                        <Grid.Row style={{ maxWidth: 450 }}>
                            <Grid.Column>
                                <Dimmer active inverted>
                                    <Loader size="large" />
                                </Dimmer>
                            </Grid.Column>
                        </Grid.Row>
                    </Grid>
                </Container>
            </div>
        )
    }

    return (
        <div>
            <Nav />
            <Container className='content'>
                <Segment raised color='teal' padded>
                    <Grid>
                        <GridRow>
                            <GridColumn>
                                <Header sub>Email:</Header>
                                <br />
                                {json.Email}
                            </GridColumn>
                        </GridRow>
                        <Divider />
                        <GridRow>
                            <Grid.Column width={8}>
                                <Button
                                    size='large'
                                    basic
                                    fluid
                                    content="Back"
                                    type='button'
                                    color="black"
                                    className="ui button"
                                    icon='arrow alternate circle left outline'
                                    onClick={handleBack} />
                            </Grid.Column>
                            <Grid.Column width={8}>
                                <Button
                                    size='large'
                                    basic
                                    fluid
                                    content="Delete"
                                    type='button'
                                    color="red"
                                    className="ui button"
                                    icon='user delete'
                                    onClick={handleDelete} />
                            </Grid.Column>
                        </GridRow>
                    </Grid>
                </Segment>
            </Container>
        </div >
    );
}
