import React, { useState, useEffect } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import axios from 'axios';
import 'semantic-ui-css/semantic.min.css';
import {
    Container,
    Grid,
    GridRow,
    Divider,
    Breadcrumb,
    List,
    Segment,
    Label,
    Loader,
    Dimmer,
    Button
} from "semantic-ui-react";
import { PoolMap } from '../Map/PoolMap';
import Nav from '../Nav/Nav';

export default function State() {

    const location = useLocation();
    const navigate = useNavigate();
    const state = location.state;

    const [json, setJson] = useState(null);
    const [loading, setLoading] = useState(true);
    const [reloadEnabled, setReloadEnabled] = useState(false);

    const pool = state.pool;
    const addr = state.addr;
    const breadcrumbName = PoolMap.get(pool);

    useEffect(() => {
        async function fetchStats() {
            try {
                const response = await axios.post('/address/stats', {
                    pool: pool,
                    addr: addr
                }, { withCredentials: true });
                setJson(response.data);
                setLoading(false);
            } catch (error) {
                console.log(error);
                navigate('/');
            }
        }
        void fetchStats();
    }, [pool, addr, navigate]);

    useEffect(() => {
        //     Reload  10    
        const timer = setTimeout(() => {
            setReloadEnabled(true);
        }, 10000);

        //     
        return () => clearTimeout(timer);
    }, []); //   ,       

    const handleBack = () => {
        navigate('/');
    };

    const handleReload = () => {
        async function fetchData() {
            try {
                const response = await axios.post('/address/stats', {
                    pool: pool,
                    addr: addr
                }, { withCredentials: true });
                setJson(response.data);
                setLoading(false);
            } catch (error) {
                console.log(error);
                navigate('/');
            }
        }
        void fetchData();
        setReloadEnabled(false);
        setTimeout(() => setReloadEnabled(true), 10000);
    };

    const Top = () => {
        return (
            <GridRow>
                <Grid.Column>
                    <Breadcrumb>
                        <Breadcrumb.Section as='a' href='/'>Home</Breadcrumb.Section>
                        <Breadcrumb.Divider />
                        <Breadcrumb.Section>{breadcrumbName.toString()}</Breadcrumb.Section>
                        <Breadcrumb.Divider />
                        <Breadcrumb.Section active>{addr.slice(0, 8) + '...' + addr.slice(-8, 8)}</Breadcrumb.Section>
                    </Breadcrumb>
                </Grid.Column>
            </GridRow>
        )
    }

    // stats
    const Stats = () => {

        const reward = json.SumRewards.map((data, idx) => {
            const r = data.Reward.split(" ");
            return (
                <List.Item key={idx}>
                    <Label>{data.Name}:</Label>
                    <Label basic color='blue'>{r[0]}</Label>
                    <Label color='green'>{r[1]} {r[2]}</Label>
                </List.Item>
            );
        })

        const balance = json.Balance.split(" ");
        const immature = json.Immature.split(" ");
        const payd = json.Payed.split(" ");

        return (
            <GridRow>
                <Grid.Column>
                    <Segment raised size='small'>
                        <Label color='teal' ribbon>Stats</Label>
                        <List size='medium'>
                            <List.Item>
                                <Label>Unpaid Balance:</Label>
                                <Label basic color='blue'>{balance[0]}</Label>
                                <Label color='green'>{balance[1]} {balance[2]}</Label>
                            </List.Item>
                            <List.Item><Label>Unconfirmed Balance:</Label>
                                <Label basic color='blue'>{immature[0]}</Label>
                                <Label color='green'>{immature[1]} {immature[2]}</Label>
                            </List.Item>

                            {reward}

                            <List.Item><Label>Total Paid:</Label>
                                <Label basic color='blue'>{payd[0]}</Label>
                                <Label color='green'>{payd[1]} {payd[2]}</Label>
                            </List.Item>
                            {json.LastBlockFound && (
                                <List.Item>
                                    <Label>Blocks Found:</Label>
                                    <Label basic color='blue'>{json.LastBlockFound}</Label>
                                </List.Item>
                            )}
                            {json.CurrentLuck && (
                                <List.Item>
                                    <Label>Personal Luck:</Label>
                                    <Label basic color='blue'>{`${json.CurrentLuck} %`}</Label>
                                </List.Item>
                            )}
                        </List>
                    </Segment>
                </Grid.Column>
            </GridRow>
        )
    };

    // workers
    const Workers = () => {

        if (json && json.Workers !== null) {

            const wrk = json.Workers.map((data, index) => {
                index++;
                return (
                    <List.Item key={index}>
                        <strong>{index}</strong>&nbsp;&nbsp;
                        <Label>{data.Name}</Label>
                        <Label basic color='blue'>{data.HR}</Label>
                        <Label color='green'>{data.LastBeat}</Label>&nbsp;&nbsp;ago
                    </List.Item>
                );
            });

            return (
                <GridRow>
                    <Grid.Column>
                        <Segment raised size='small'>
                            <Label color='teal' ribbon>Workers</Label>
                            <List size='medium'>
                                {wrk}
                                <Divider />
                                <List.Item><Label>Average Hashrate (6 hrs):</Label>
                                    <Label basic color='blue'>{json.HashRate}</Label>
                                </List.Item>
                                <List.Item><Label>Current hashrate (30 min):</Label>
                                    <Label basic color='blue'>{json.CurrentHashRate}</Label>
                                </List.Item>
                            </List>
                        </Segment>
                    </Grid.Column>
                </GridRow>
            )
        }
    };

    if (loading) {
        return (
            <div>
                <Container className='content'>
                    <Grid textAlign='center' style={{ height: '50vh' }} verticalAlign='middle'>
                        <Grid.Row style={{ maxWidth: 450 }}>
                            <Grid.Column>
                                <Dimmer active inverted>
                                    <Loader size="large" />
                                </Dimmer>
                            </Grid.Column>
                        </Grid.Row>
                    </Grid>
                </Container>
            </div>
        )
    }

    const Content = () => {
        return (
            <div>
                <Nav />
                <Container className='content'>
                    <Grid>
                        <Top />
                        <Workers />
                        <Stats />
                        <GridRow>
                            <Grid.Column width={8}>
                                <Button
                                    size='large'
                                    basic
                                    fluid
                                    content="Back"
                                    type='button'
                                    color="black"
                                    className="ui button"
                                    icon='arrow alternate circle left outline'
                                    onClick={handleBack} />
                            </Grid.Column>
                            <Grid.Column width={8}>
                                <Button
                                    size='large'
                                    basic
                                    fluid
                                    content="Reload"
                                    type='button'
                                    color="black"
                                    className="ui button"
                                    icon='history'
                                    onClick={handleReload}
                                    disabled={!reloadEnabled} />
                            </Grid.Column>
                        </GridRow>
                    </Grid>
                </Container>
            </div>
        )
    };

    return (
        <div>
            {json.toString() && <Content />}
        </div >
    );
}
