import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import 'semantic-ui-css/semantic.min.css';
import {
    Form,
    Button,
    GridRow,
    Grid,
    Container,
    Checkbox,
    Dimmer,
    Loader
} from "semantic-ui-react";
import Nav from '../Nav/Nav';

export default function Create() {

    const navigate = useNavigate();
    const [pool, setPool] = useState('');
    const [addr, setAddr] = useState('');
    const [loading, setLoading] = useState(true);
    const [input, setInput] = useState(false);
    const [notify, setNotify] = useState(5);
    const [monitor, setMonitor] = useState();
    const [block, setBlock] = useState(false);

    const options = [
        { key: 'rvn', text: 'Ravencoin', value: 'rvn' },
        { key: 'solo-rvn', text: 'Ravencoin SOLO', value: 'solo-rvn' },
        { key: 'etc', text: 'Ethereum Classic', value: 'etc' },
        { key: 'solo-etc', text: 'Ethereum Classic SOLO', value: 'solo-etc' },
        { key: 'xna', text: 'Neurai', value: 'xna' },
        { key: 'solo-xna', text: 'Neurai SOLO', value: 'solo-xna' },
        { key: 'erg', text: 'Ergo', value: 'erg' },
        { key: 'solo-erg', text: 'Ergo SOLO', value: 'solo-erg' },
        { key: 'btg', text: 'Bitcoin GOLD', value: 'btg' },
        { key: 'solo-btg', text: 'Bitcoin GOLD SOLO', value: 'solo-btg' },
        { key: 'kas', text: 'Kaspa', value: 'kas' },
        { key: 'solo-kas', text: 'Kaspa SOLO', value: 'solo-kas' },
        { key: 'xmr', text: 'Monero', value: 'xmr' },
        { key: 'solo-xmr', text: 'Monero SOLO', value: 'solo-xmr' },
        { key: 'clore', text: 'Clore.ai', value: 'clore' },
        { key: 'solo-clore', text: 'Clore.ai SOLO', value: 'solo-clore' },
        { key: 'ctxc', text: 'Cortex', value: 'ctxc' },
        { key: 'solo-ctxc', text: 'Cortex SOLO', value: 'solo-ctxc' },
        { key: 'neox', text: 'Neoxa', value: 'neox' },
        { key: 'solo-neox', text: 'Neoxa SOLO', value: 'solo-neox' },
        { key: 'ethw', text: 'Ethereum PoW', value: 'ethw' },
        { key: 'solo-ethw', text: 'Ethereum PoW SOLO', value: 'solo-ethw' },
        { key: 'nexa', text: 'Nexa', value: 'nexa' },
        { key: 'solo-nexa', text: 'Nexa SOLO', value: 'solo-nexa' },
        { key: 'kls', text: 'Karlsen', value: 'kls' },
        { key: 'solo-kls', text: 'Karlsen SOLO', value: 'solo-kls' },
        { key: 'grin', text: 'GRIN', value: 'grin' },
        { key: 'solo-grin', text: 'GRIN SOLO', value: 'solo-grin' },
        { key: 'beam', text: 'Beam', value: 'beam' },
        { key: 'solo-beam', text: 'Beam SOLO', value: 'solo-beam' },
        { key: 'firo', text: 'Firo (Zcoin)', value: 'firo' },
        { key: 'solo-firo', text: 'Firo (Zcoin) SOLO', value: 'solo-firo' },
        { key: 'zec', text: 'Zcash', value: 'zec' },
        { key: 'solo-zec', text: 'Zcash SOLO', value: 'solo-zec' },
        { key: 'ckb', text: 'Nervos', value: 'ckb' },
        { key: 'solo-ckb', text: 'Nervos SOLO', value: 'solo-ckb' },
        { key: 'flux', text: 'Flux (ZelCash)', value: 'flux' },
        { key: 'solo-flux', text: 'Flux (ZelCash) SOLO', value: 'solo-flux' },
        { key: 'pyi', text: 'Pyrin', value: 'pyi' },
        { key: 'solo-pyi', text: 'Pyrin SOLO', value: 'solo-pyi' },
        { key: 'zen', text: 'Horizen', value: 'zen' },
        { key: 'solo-zen', text: 'Horizen SOLO', value: 'solo-zen' },
        { key: 'ae', text: 'Aeternity', value: 'ae' },
        { key: 'solo-ae', text: 'Aeternity SOLO', value: 'solo-ae' },
        { key: 'bch', text: 'Bitcoin Cash', value: 'bch' },
        { key: 'solo-bch', text: 'Bitcoin Cash SOLO', value: 'solo-bch' },
    ];

    useEffect(() => {
        async function fetchData() {
            try {
                await axios.get('/account', {
                    responseType: 'json',
                    withCredentials: true,
                });
                setLoading(false);
            } catch (error) {
                navigate('/login');
            }
        }
        void fetchData();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [navigate, setLoading]);

    const handleSelectPool = (_, data) => {
        setPool(data.value);
    };

    const handleBack = () => {
        navigate('/');
    };

    const handleCheckedBlock = () => {
        setBlock(!block);
    };

    const handleCheckedMonitor = async () => {
        setMonitor(!monitor);
    };

    const handleSubmit = () => {
        axios.post('/address', {
            pool: pool,
            addr: addr,
            notify: Number(notify),
            monitoraddr: monitor,
            monitorblock: block,
        }, { withCredentials: true, }
        ).then(() => {
            navigate('/');
        }).catch(() => {
            setInput(true);
        });
    };

    if (loading) {
        return (
            <div>
                <Container className='content'>
                    <Grid textAlign='center' style={{ height: '50vh' }} verticalAlign='middle'>
                        <Grid.Row style={{ maxWidth: 450 }}>
                            <Grid.Column>
                                <Dimmer active inverted>
                                    <Loader size="large" />
                                </Dimmer>
                            </Grid.Column>
                        </Grid.Row>
                    </Grid>
                </Container>
            </div>
        )
    }


    return (
        <div>
            <Form>
                <Nav />
                <Container className='content'>
                    <Grid>
                        <GridRow>
                            <Grid.Column>
                                <Form onSubmit={handleSubmit}>
                                    <Form.Field>
                                        <Form.Select
                                            required
                                            label='Select Pool'
                                            options={options}
                                            placeholder='Ethereum PoW'
                                            value={pool}
                                            onChange={handleSelectPool} />
                                        <Form.Input
                                            label='Paste Address'
                                            required
                                            placeholder='0x7c29C787eD8Ad173352d1f0c923dD43dfC2516f5'
                                            value={addr}
                                            error={input}
                                            onChange={event => setAddr(event.target.value)} />
                                    </Form.Field>
                                </Form>
                            </Grid.Column>
                        </GridRow>


                        <GridRow>
                            <Grid.Column>

                                <Form.Field>
                                    <div>Send email if a worker goes offline over {notify} minutes</div>
                                    <Form.Input
                                        type='range'
                                        min={1}
                                        max={60}
                                        value={notify}
                                        onChange={event => setNotify(event.target.value)}
                                    />
                                </Form.Field>
                            </Grid.Column>
                        </GridRow>


                        <GridRow>
                            <Grid.Column width="8">
                                <Form.Field>
                                    <Checkbox
                                        label='Monitoring'
                                        checked={monitor}
                                        onChange={handleCheckedMonitor} />
                                </Form.Field>
                            </Grid.Column>
                            <Grid.Column width="8">
                                <Form.Field>
                                    <Checkbox
                                        label='New blocks'
                                        value={block}
                                        onChange={handleCheckedBlock} />
                                </Form.Field>
                            </Grid.Column>
                        </GridRow>

                        <GridRow>
                            <Grid.Column width={8}>
                                <Button
                                    size='large'
                                    basic
                                    fluid
                                    content='Back'
                                    type='button'
                                    color="black"
                                    className="ui button"
                                    icon='arrow alternate circle left outline'
                                    onClick={handleBack} />
                            </Grid.Column>
                            <Grid.Column width={8}>
                                <Button
                                    size='large'
                                    icon='send'
                                    basic
                                    fluid
                                    content="Submit"
                                    color='black'
                                    type='button'
                                    floated="right"
                                    disabled={!addr}
                                    onClick={handleSubmit} />
                            </Grid.Column>
                        </GridRow>
                    </Grid>
                </Container>
            </Form>
        </div>
    );
}

